import find from "lodash/find"

export const colors = [
  { id: 1, value: "#000001", label: "Color #1" },
  { id: 2, value: "#000001", label: "Color #2" },
  { id: 3, value: "#000001", label: "Color #3" },
  { id: 4, value: "#000001", label: "Color #4" },
  { id: 5, value: "#000001", label: "Color #5" },
  { id: 6, value: "#000001", label: "Color #6" },
]

export const flooringColors = [
  { id: 1, value: "#000001", label: "Floor #1" },
  { id: 2, value: "#000001", label: "Floor #2" },
  { id: 3, value: "#000001", label: "Floor #3" },
  { id: 4, value: "#000001", label: "Floor #4" },
  { id: 5, value: "#000001", label: "Floor #5" },
  { id: 6, value: "#000001", label: "Floor #6" },
]

export function getColorById(id) {
  return find(colors, { id: parseInt(id) })
}

export function getFloorById(id) {
  return find(flooringColors, { id: parseInt(id) })
}
